export default {
  methods: {
    calculate: function(localInputs) {
      var bandSeries = {};

      var start = localInputs.transactionDate;

      if (localInputs.transactionType == "Residential") {
        if (localInputs.transactionRate == "MainRates") {
          bandSeries = this.rates.residential; //Main residential tax rates
        } else if (localInputs.transactionRate == "HigherRates") {
          bandSeries = this.rates.higher; //Higher residential tax rates
        }
      } else if (localInputs.transactionType == "NonResidential") {
        bandSeries = this.rates.nonResidential; //Non-residential property tax rates

        if (localInputs.propertyType == "NewLease") {
          //Find applicable relevant rent
          var aptRelevantRent = this.relevantRent.find(
            (element) =>
              this.getDate(element.startDate) <= this.getDate(start) &&
              this.getDate(element.endDate) >= this.getDate(start)
          );
          //Find applicable relevant rent rates
          var aptRates = bandSeries.find(
            (element) =>
              this.getDate(element.startDate) <= this.getDate(start) &&
              this.getDate(element.endDate) >= this.getDate(start)
          );

          if (
            localInputs.relevantRent.replaceAll(",", "") >=
            aptRelevantRent.value
          ) {
            //Remove first band
            aptRates.bands.shift();

            //Remove second band, modify (set min to 0), put back in
            var band = aptRates.bands.shift();
            band.min = 1;
            aptRates.bands.unshift(band);
          }
        }
      }

      var relevantRates = bandSeries.find(
        (element) =>
          this.getDate(element.startDate) <= this.getDate(start) &&
          this.getDate(element.endDate) >= this.getDate(start)
      );

      var relevantNpvRates = this.rates.netPresentValue.find(
        (element) =>
          this.getDate(element.startDate) <= this.getDate(start) &&
          this.getDate(element.endDate) >= this.getDate(start)
      );

      this.bands = relevantRates.bands;
      this.npvBands = relevantNpvRates.bands;

      var purchasePrice = String(localInputs.purchasePrice).replaceAll(",", ""); //Remove commas from purchasePrice

      if (this.localInputs.propertyType == "NewLease") {
        var relevantRent = this.localInputs.relevantRent.replaceAll(",", "");
        //If purchasePrice is less than 40,000 AND relevantRent is less than the threshold
        //Only then do you set purchasePrice to 0
        //If relevantRent >= the threshold, you must pay tax on the premium
        if (purchasePrice < 40000 && relevantRent < aptRelevantRent.value) {
          purchasePrice = 0;
        }
      } else {
        if (purchasePrice < 40000) {
          purchasePrice = 0;
        }
      }

      var taxBasedOnPremium = 0;

      this.describeBands();

      //taxBasedOnPremium
      this.bands.forEach(function(band) {
        if (purchasePrice < band.min) {
          band.amount = 0;
        } else if (band.max > purchasePrice) {
          band.amount = purchasePrice - band.min + 1;
        } else {
          band.amount = band.max - band.min + 1;
        }

        band.tax = Math.floor(band.amount * band.rate) / 100;

        taxBasedOnPremium += band.tax;
      }, this);

      this.taxBasedOnPremium = taxBasedOnPremium;
    }
  }
};
