export default {
  methods: {
    calculateNpv: function(localInputs) {
      var rentEachYear = [];
      var taxBasedOnNpv = 0;

      //Add rent values to rentEachYear - convert to Numbers & remove thousands separators
      rentEachYear.push(Number(localInputs.rentYearOne.replaceAll(",", "")));

      if (
        localInputs.rentYearTwo != null &&
        this.leaseDuration.yearsSpanned >= 2
      ) {
        rentEachYear.push(Number(localInputs.rentYearTwo.replaceAll(",", "")));
      }
      if (
        localInputs.rentYearThree != null &&
        this.leaseDuration.yearsSpanned >= 3
      ) {
        rentEachYear.push(
          Number(localInputs.rentYearThree.replaceAll(",", ""))
        );
      }
      if (
        localInputs.rentYearFour != null &&
        this.leaseDuration.yearsSpanned >= 4
      ) {
        rentEachYear.push(Number(localInputs.rentYearFour.replaceAll(",", "")));
      }
      if (
        localInputs.rentYearFive != null &&
        this.leaseDuration.yearsSpanned >= 5
      ) {
        rentEachYear.push(Number(localInputs.rentYearFive.replaceAll(",", "")));
      }

      //Remove 5 years from yearsInLease (because we already have those values)
      //Loop through yearsInLease, adding Highest Rent figure to array each time
      var yearsInLease = this.leaseDuration.fullYears - 5;
      for (let i = 0; i < yearsInLease; i++) {
        rentEachYear.push(Number(localInputs.highestRent.replaceAll(",", "")));
      }

      //Work out how much of the annual Highest Rent figure is applicable to the remaining day/s
      //Add to rentEachYear array
      if (
        this.leaseDuration.remainingDays > 0 &&
        localInputs.highestRent != null &&
        this.leaseDuration.fullYears >= 5
      ) {
        //Calculate whether the last year of the lease is 365 or 366 days
        const utcLastFullYear = Date.UTC(
          this.leaseDuration.fullYearEnd.getFullYear(),
          this.leaseDuration.fullYearEnd.getMonth(),
          this.leaseDuration.fullYearEnd.getDate()
        );
        const utcYearAfterLast = Date.UTC(
          this.leaseDuration.fullYearEnd.getFullYear() + 1,
          this.leaseDuration.fullYearEnd.getMonth(),
          this.leaseDuration.fullYearEnd.getDate()
        );

        //Calculate the diff between the 2 dates
        var lastYearDays = Math.ceil(
          (utcYearAfterLast - utcLastFullYear) / (1000 * 60 * 60 * 24)
        );

        //Divide the remaining days by the number of days in the last year of the lease (365 or 366)
        var ratio = this.leaseDuration.remainingDays / lastYearDays;

        rentEachYear.push(localInputs.highestRent.replaceAll(",", "") * ratio);
      }

      var npv = 0;

      //Loop through rentEachYear array and add up the figures
      rentEachYear.forEach((item, index) => {
        //@RentYear1/(power((1+@NPVRate),1))
        var npvEachYear = item / (1 + 0.035) ** (index + 1);
        npv = npv + npvEachYear;
      });

      this.npv = Math.floor(npv);

      this.describeNpvBands();

      //taxBasedOnNpv
      this.npvBands.forEach(function(band) {
        if (this.npv < band.min) {
          band.amount = 0;
        } else if (band.max > this.npv) {
          band.amount = this.npv - band.min + 1;
        } else {
          band.amount = band.max - band.min + 1;
        }

        band.tax = Math.floor(band.amount * band.rate) / 100;

        taxBasedOnNpv += band.tax;
      }, this);

      this.taxBasedOnNpv = taxBasedOnNpv;
    }
  }
};
