<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
    v-if="this.totalTax != null"
  >
    <transition name="translate" mode="out-in">
      <div :key="text.taxCalculation.title">
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{ text.taxCalculation.title }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.taxCalculation.description"></p>
          </wra-card-text>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <wra-card-text class="calc-result">
            <v-row class="ma-0">
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalTaxDue }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatCurrency(totalTax) }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.effectiveDate }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  {{ localInputs.transactionDate }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalPurchasePrice }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ localInputs.considerationDwellings }}
                </div>
              </v-col>
            </v-row>
          </wra-card-text>
        </wra-card-text>

        <!-- 'Summary of your answers' expansion panel -->
        <wra-card-text class="pa-4">
          <wra-expansion-panels flat v-model="summaryPanel">
            <wra-expansion-panel>
              <wra-expansion-panel-header>
                {{ text.taxCalculation.answerSummary.title }}
              </wra-expansion-panel-header>

              <wra-expansion-panel-content class="pt-0">
                <wra-simple-table>
                  <template v-slot:default>
                    <caption>
                      {{
                        text.taxCalculation.answerSummary.title
                      }}
                    </caption>
                    <tbody>
                      <!--TransactionRate-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionRate
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'HigherRates'"
                        >
                          {{ text.transactionRate.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'MainRates'"
                        >
                          {{ text.transactionRate.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionRate')"
                            id="transactionRateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--TransactionDate-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionDate
                          }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.transactionDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionDate')"
                            id="transactionDateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--PurchasePrice-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.totalPurchasePrice
                          }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.considerationDwellings }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('ConsiderationDwellings')"
                            id="purchasePriceEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--NumberOfDwellings-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.numberOfDwellings
                          }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.numberOfDwellings }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('NumberOfDwellings')"
                            id="numberOfDwellingsEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </wra-simple-table>
              </wra-expansion-panel-content>
            </wra-expansion-panel>
          </wra-expansion-panels>
        </wra-card-text>

        <!-- Detailed calculation -->
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{
              text.taxCalculation.calcBreakdown.title
            }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.description"></p>
          </wra-card-text>

          <wra-card-text class="px-0 pb-4">
            <wra-header size="h3">
              {{ text.taxCalculation.calcBreakdown.explanation.stepOne }}
            </wra-header>
          </wra-card-text>

          <wra-card-text class="pt-0 px-0 pb-4">
            <p>
              {{
                text.taxCalculation.calcBreakdown.explanation.pricePerDwelling
              }}
              <b>&pound;{{ formatUseGrouping(localInputs.purchasePrice) }}</b
              >.
            </p>
            <p>
              {{ text.taxCalculation.calcBreakdown.explanation.breakdownTable }}
              <b>{{
                text.taxCalculation.calcBreakdown.explanation.taxDuePerDwelling
              }}</b
              >.
            </p>
          </wra-card-text>
        </wra-card-text>

        <!-- Purchase Price Bands Table (LTT on the premium) -->
        <wra-card-text class="pt-0 px-0 pb-4">
          <wra-data-table
            :headers="basedOnPremiumHeaders"
            :items="bands"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:item.amount="{ item }">
              {{ formatUseGrouping(item.amount) }}
            </template>

            <template v-slot:item.tax="{ item }">
              {{ formatCurrency(item.tax) }}
            </template>

            <template v-slot:foot>
              <tfoot>
                <tr class="mdr-calc-breakdown-total">
                  <td colspan="4" class="text-right">
                    {{ text.taxCalculation.calcBreakdown.totalTaxDue }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnPremium) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <wra-card-text class="pt-0 px-0 pb-4">
            <wra-header size="h3">
              {{ text.taxCalculation.calcBreakdown.explanation.stepTwo }}
            </wra-header>
          </wra-card-text>

          <p>
            {{ text.taxCalculation.calcBreakdown.explanation.oneDwelling }}
          </p>
          <p>
            {{ text.taxCalculation.calcBreakdown.explanation.youArePurchasing }}
            <b>
              {{ localInputs.numberOfDwellings }}
              {{ text.taxCalculation.calcBreakdown.explanation.dwellings }}</b
            >, {{ text.taxCalculation.calcBreakdown.explanation.soYour }}
            <b>
              {{ text.taxCalculation.calcBreakdown.explanation.totalTaxDue }}
              &pound;{{ formatCurrency(this.totalTax) }}</b
            >.
          </p>
        </wra-card-text>

        <wra-card-text class="pt-0 px-4 pb-4" v-if="this.isOnePercent == true">
          <p>
            {{
              text.taxCalculation.calcBreakdown.explanation.lessThanOnePercent
            }}
          </p>
          <p>
            {{ text.taxCalculation.calcBreakdown.explanation.therefore }},
            <b>{{
              text.taxCalculation.calcBreakdown.explanation.payOnePercent
            }}</b
            >, {{ text.taxCalculation.calcBreakdown.explanation.whichIs }}
            <b>&pound;{{ formatCurrency(this.onePercent) }}</b
            >.
          </p>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <p>
            {{ text.taxCalculation.calcBreakdown.payLtt.textP1 }}
            <a
              :href="text.taxCalculation.calcBreakdown.payLtt.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.calcBreakdown.payLtt.linkText +
                  ' (' +
                  text.taxCalculation.calcBreakdown.payLtt.newTab +
                  ')'
              "
              >{{ text.taxCalculation.calcBreakdown.payLtt.linkText }}</a
            >
            {{ text.taxCalculation.calcBreakdown.payLtt.textP2 }}
          </p>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <wra-button outlined @click="printClicked()" id="printButton">
            {{ text.taxCalculation.print }}
          </wra-button>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <p>
            <a
              :href="text.taxCalculation.feedback.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.feedback.text +
                  ' (' +
                  text.taxCalculation.feedback.newTab +
                  ')'
              "
              >{{ text.taxCalculation.feedback.text }}</a
            >
          </p>
        </wra-card-text>

        <wra-card-actions class="pt-4 px-4 pb-2 hidden-print">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="firstStage()"
                id="restartButton"
              >
                {{ text.restart }}
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </div>
    </transition>
  </wra-card>
</template>

<script>
const axios = require("axios");

import lttCalculation from "../../mixins/ltt-calculation.js";
import getDate from "../../mixins/get-date.js";
import describeBands from "../../mixins/describe-bands.js";
import formatUseGrouping from "../../mixins/format-use-grouping.js";
import formatCurrency from "../../mixins/format-currency.js";
import calcBreakdownHeaders from "../../mixins/calc-breakdown-headers.js";

export default {
  props: ["text", "inputs"],

  mixins: [
    lttCalculation,
    getDate,
    describeBands,
    formatUseGrouping,
    formatCurrency,
    calcBreakdownHeaders
  ],

  data: () => ({
    localInputs: {},
    rates: null,
    bands: [],
    npvBands: [],
    totalTax: null,
    taxBasedOnPremium: null,
    summaryPanel: [],
    onePercent: null,
    isOnePercent: false
  }),
  watch: {
    text: {
      deep: true
    }
  },
  methods: {
    previousStage: function() {
      //this.$ga.event("MDR Calculator", "", "Back");

      this.$emit("previous", this.localInputs);
    },
    firstStage: function() {
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //   this.$ga.event("MDR Calculator", "Start again");
      // }
      this.$emit("reset");
    },
    editStage: function(stage) {
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //   this.$ga.event("MDR Calculator", "Edit", stage);
      // }
      this.$emit("goToStage", {
        localInputs: this.localInputs,
        componentName: stage
      });
    },
    printClicked: function() {
      // if (this.$root.$data.cookieAccept.websiteUse == "true") {
      //   this.$ga.event("MDR Calculator", "Print or Save");
      // }
      window.print();
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.totalTax = null; // Reset the tax amount to null before calculating (or recalculating on second visit)
    axios.get("./rates/rates.json").then((response) => {
      this.localInputs.transactionType = "Residential";

      // Divide purchasePrice by numberOfDwellings to get price of each dwelling
      this.localInputs.purchasePrice = Math.floor(
        this.localInputs.considerationDwellings.replaceAll(",", "") /
          this.localInputs.numberOfDwellings.replaceAll(",", "")
      );

      this.rates = response.data.rates;

      var taxSum = 0;
      var len = this.localInputs.numberOfDwellings;

      // Put each dwelling through the calculate function
      // Add up the total tax
      for (var i = 0; i < len; i++) {
        this.calculate(this.localInputs);
        taxSum += this.taxBasedOnPremium;
      }

      this.totalTax = taxSum;

      // 1% rule
      // Calculate 1% of total consideration
      this.onePercent =
        this.localInputs.considerationDwellings.replaceAll(",", "") * 0.01;
      // If totalTax is less than 1% of total consideration...
      if (this.totalTax < this.onePercent) {
        // Set totalTax to onePercent
        this.totalTax = this.onePercent;
        this.isOnePercent = true;
      }

      this.$nextTick(() => {
        this.$emit("scrollUp");
      });
    });
  }
};
</script>

<style scoped>
.v-input {
  font-size: 18px;
}
</style>
