<template>
  <form @submit.prevent="nextStage">
    <wra-card
      :class="{
        'px-1 py-1': $vuetify.breakpoint.smAndDown,
        'px-6 py-5': $vuetify.breakpoint.mdAndUp
      }"
      color="#F1F1F1"
      outlined
    >
      <transition name="translate" mode="out-in">
        <wra-card-text
          v-if="showValidation"
          class="pa-4"
          :key="text.rentInformation.title"
        >
          <wra-validation-tooltip
            type="wra-error"
            role="status"
            aria-live="polite"
          >
            {{ validationMessage }}
          </wra-validation-tooltip>
        </wra-card-text>
      </transition>

      <transition name="translate" mode="out-in">
        <wra-card-text class="pa-4" :key="text.rentInformation.title">
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{ text.purchasePrice.title }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-8">
            <p v-html="text.purchasePrice.description"></p>
          </wra-card-text>
          <wra-card-text class="hint-text pa-0">
            <p v-html="text.purchasePrice.hint"></p>
          </wra-card-text>
          <wra-text-field
            v-model="localInputs.purchasePrice"
            v-mask="mask"
            prefix="£"
            class="ltt-price-field"
            inputmode="numeric"
            id="priceField"
          ></wra-text-field>
        </wra-card-text>
      </transition>

      <transition name="translate" mode="out-in">
        <wra-card-actions class="py-2 px-4" :key="text.rentInformation.title">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                color="#019e1e"
                class="mb-2"
                id="calculateButton"
                type="submit"
              >
                {{ text.calculate }}
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </transition>
    </wra-card>
  </form>
</template>

<script>
import createNumberMask from "text-mask-addons/dist/createNumberMask";
const currencyMask = createNumberMask({
  prefix: "",
  allowDecimal: false,
  includeThousandsSeparator: true,
  allowNegative: false
});
export default {
  props: ["text", "inputs"],

  data: () => ({
    showValidation: false,
    isValid: true,
    validationMessage: null,
    localInputs: {},
    mask: currencyMask
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.validatePage();
      }
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "What is the purchase price?", "Back");
      }
      this.$emit("previous", this.localInputs);
    },
    nextStage: function() {
      this.validatePage();

      if (this.isValid == true) {
        this.showValidation = false;

        if (this.$root.$data.cookieAccept.websiteUse == "true") {
          this.$ga.event(
            "LTT Calculator",
            "What is the purchase price?",
            this.localInputs.purchasePrice
          );
        }

        this.$emit("next", this.localInputs);
      } else {
        this.showValidation = true;
        this.$emit("scrollUp");
      }
    },
    validatePage: function() {
      if (
        this.localInputs.purchasePrice == null ||
        this.localInputs.purchasePrice == ""
      ) {
        this.validationMessage = this.text.purchasePrice.validation.invalidPrice;
        this.isValid = false;
      } else if (
        parseInt(this.localInputs.purchasePrice.replaceAll(",", "")) >
        999999999999
      ) {
        this.validationMessage = this.text.purchasePrice.validation.aboveMax;
        this.isValid = false;
      } else {
        this.isValid = true;
      }
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.$emit("scrollUp");
  }
};
</script>

<style scoped>
.v-input {
  font-size: 18px;
}
</style>
