export default {
  computed: {
    basedOnPremiumHeaders: function() {
      return [
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.priceBands +
            " (£)",
          value: "description"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.percentageRate +
            " (%)",
          value: "rate",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.amount + " (£)",
          value: "amount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "tax",
          align: "right"
        }
      ];
    }
  }
};
