<template>
  <v-container
    class="pt-0 pb-3"
    :class="{
      'px-0': $vuetify.breakpoint.smAndDown
    }"
  >
    <!--Heading-->
    <v-row
      no-gutters
      class="pb-6 hidden-print"
      :class="{
        'px-5': $vuetify.breakpoint.smAndDown
      }"
    >
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12">
        <v-divider
          role="presentation"
          style="height: 6px;
          max-height: 6px;
          background: #e5e5e5;
          border: none;"
        ></v-divider>
        <transition name="translate" mode="out-in">
          <wra-subheader class="mt-8" :key="text.header">
            {{ text.subheader }}
          </wra-subheader>
        </transition>
        <transition name="translate" mode="out-in">
          <wra-header size="h1" :key="text.header">
            {{ text.header }}
          </wra-header>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>

    <!--Card-->
    <v-row no-gutters>
      <v-col lg="2" cols="0"></v-col>
      <v-col lg="8" cols="12" class="minimum-height">
        <transition name="fade" mode="out-in">
          <component
            id="questionCard"
            :is="componentName"
            v-if="loaded && textLoaded"
            :text="text"
            :inputs="inputs"
            v-on:previous="previousStage"
            v-on:next="nextStage"
            v-on:goToStage="goToStage"
            v-on:reset="reset"
            v-on:scrollUp="scrollUp"
          ></component>
        </transition>
      </v-col>
      <v-col lg="2" cols="0"></v-col>
    </v-row>
  </v-container>
</template>

<script>
import TransactionRate from "../components/mdr-calculator/TransactionRate.vue";
import TransactionDate from "../components/mdr-calculator/TransactionDate.vue";
import ConsiderationDwellings from "../components/mdr-calculator/ConsiderationDwellings.vue";
import NumberOfDwellings from "../components/mdr-calculator/NumberOfDwellings.vue";
import IsSubsidiaryDwelling from "../components/mdr-calculator/IsSubsidiaryDwelling.vue";
import NumberOfSubsidiaryDwellings from "../components/mdr-calculator/NumberOfSubsidiaryDwellings.vue";
import ConsiderationSubsidiaryDwellings from "../components/mdr-calculator/ConsiderationSubsidiaryDwellings.vue";
import IsOtherLand from "../components/mdr-calculator/IsOtherLand.vue";
import ConsiderationOtherLand from "../components/mdr-calculator/ConsiderationOtherLand.vue";
import OtherLandType from "../components/mdr-calculator/OtherLandType.vue";
import IsLinkedTransaction from "../components/mdr-calculator/IsLinkedTransaction.vue";
import ConsiderationLinkedTransaction from "../components/mdr-calculator/ConsiderationLinkedTransaction.vue";
import TaxCalculation from "../components/mdr-calculator/TaxCalculation.vue";

const axios = require("axios");

export default {
  props: ["lang"],

  data: () => ({
    componentName: "",
    loaded: false,
    textLoaded: false,
    text: {
      transactionType: {
        answers: [{ label: {} }]
      },
      helpGuidance: {}
    },
    inputs: {
      transactionType: null,
      purchasePrice: null,

      transactionRate: null,
      transactionDate: null,
      considerationDwellings: null,
      numberOfDwellings: null
      // isSubsidiaryDwelling: null,
      // numberOfSubsidiaryDwellings: null,
      // considerationSubsidiaryDwellings: null,
      // isOtherLand: null,
      // considerationOtherLand: null,
      // otherLandType: null,
      // isLinkedTransaction: null,
      // considerationLinkedTransaction: null
    }
  }),
  watch: {
    lang: {
      handler() {
        this.getText();
      }
    }
  },
  methods: {
    getText: function() {
      axios
        .get("./text/" + this.lang + "/mdrcalculator.json")
        .then((response) => {
          this.text = response.data.text;
          this.textLoaded = true;
          document.title = this.text.documentTitleText;
        });
    },
    setStage: function() {
      this.componentName = "TransactionRate";
      this.loaded = true;
    },
    previousStage: function(localInputs) {
      this.inputs = localInputs;

      // if (
      //   this.componentName == "TaxCalculation" &&
      //   this.inputs.isLinkedTransaction == "True"
      // ) {
      //   this.componentName = "ConsiderationLinkedTransaction";
      // } else if (
      //   this.componentName == "TaxCalculation" &&
      //   this.inputs.isLinkedTransaction == "False"
      // ) {
      //   this.componentName = "IsLinkedTransaction";
      // } else if (this.componentName == "ConsiderationLinkedTransaction") {
      //   this.componentName = "IsLinkedTransaction";
      // } else if (
      //   this.componentName == "IsLinkedTransaction" &&
      //   this.inputs.isOtherLand == "True"
      // ) {
      //   this.componentName = "OtherLandType";
      // } else if (
      //   this.componentName == "IsLinkedTransaction" &&
      //   this.inputs.isOtherLand == "False"
      // ) {
      //   this.componentName = "IsOtherLand";
      // } else if (this.componentName == "OtherLandType") {
      //   this.componentName = "ConsiderationOtherLand";
      // } else if (this.componentName == "ConsiderationOtherLand") {
      //   this.componentName = "IsOtherLand";
      // } else if (
      //   this.componentName == "IsOtherLand" &&
      //   this.inputs.isSubsidiaryDwelling == "True"
      // ) {
      //   this.componentName = "ConsiderationSubsidiaryDwellings";
      // } else if (
      //   this.componentName == "IsOtherLand" &&
      //   this.inputs.isSubsidiaryDwelling == "False"
      // ) {
      //   this.componentName = "IsSubsidiaryDwelling";
      // } else if (this.componentName == "ConsiderationSubsidiaryDwellings") {
      //   this.componentName = "NumberOfSubsidiaryDwellings";
      // } else if (this.componentName == "NumberOfSubsidiaryDwellings") {
      //   this.componentName = "IsSubsidiaryDwelling";
      // } else if (this.componentName == "IsSubsidiaryDwelling") {
      //   this.componentName = "NumberOfDwellings";
      // }
      if (this.componentName == "TaxCalculation") {
        this.componentName = "NumberOfDwellings";
      } else if (this.componentName == "NumberOfDwellings") {
        this.componentName = "ConsiderationDwellings";
      } else if (this.componentName == "ConsiderationDwellings") {
        this.componentName = "TransactionDate";
      } else if (this.componentName == "TransactionDate") {
        this.componentName = "TransactionRate";
      }
    },
    nextStage: function(localInputs) {
      this.inputs = localInputs;

      if (this.componentName == "TransactionRate") {
        this.componentName = "TransactionDate";
      } else if (this.componentName == "TransactionDate") {
        this.componentName = "ConsiderationDwellings";
      } else if (this.componentName == "ConsiderationDwellings") {
        this.componentName = "NumberOfDwellings";
      } else if (this.componentName == "NumberOfDwellings") {
        this.componentName = "TaxCalculation";
      }
      // else if (this.componentName == "NumberOfDwellings") {
      //   this.componentName = "IsSubsidiaryDwelling";
      // } else if (
      //   this.componentName == "IsSubsidiaryDwelling" &&
      //   this.inputs.isSubsidiaryDwelling == "True"
      // ) {
      //   this.componentName = "NumberOfSubsidiaryDwellings";
      // } else if (
      //   this.componentName == "IsSubsidiaryDwelling" &&
      //   this.inputs.isSubsidiaryDwelling == "False"
      // ) {
      //   this.componentName = "IsOtherLand";
      // } else if (this.componentName == "NumberOfSubsidiaryDwellings") {
      //   this.componentName = "ConsiderationSubsidiaryDwellings";
      // } else if (this.componentName == "ConsiderationSubsidiaryDwellings") {
      //   this.componentName = "IsOtherLand";
      // } else if (
      //   this.componentName == "IsOtherLand" &&
      //   this.inputs.isOtherLand == "True"
      // ) {
      //   this.componentName = "ConsiderationOtherLand";
      // } else if (
      //   this.componentName == "IsOtherLand" &&
      //   this.inputs.isOtherLand == "False"
      // ) {
      //   this.componentName = "IsLinkedTransaction";
      // } else if (this.componentName == "ConsiderationOtherLand") {
      //   this.componentName = "OtherLandType";
      // } else if (this.componentName == "OtherLandType") {
      //   this.componentName = "IsLinkedTransaction";
      // } else if (
      //   this.componentName == "IsLinkedTransaction" &&
      //   this.inputs.isLinkedTransaction == "True"
      // ) {
      //   this.componentName = "ConsiderationLinkedTransaction";
      // } else if (
      //   this.componentName == "IsLinkedTransaction" &&
      //   this.inputs.isLinkedTransaction == "False"
      // ) {
      //   this.componentName = "TaxCalculation";
      // } else if (this.componentName == "ConsiderationLinkedTransaction") {
      //   this.componentName = "TaxCalculation";
      // }
    },
    goToStage: function(inputState) {
      this.inputs = inputState.localInputs;
      this.componentName = inputState.componentName;
    },
    reset: function() {
      this.inputs = {
        transactionRate: null,
        transactionDate: null,
        considerationDwellings: null,
        numberOfDwellings: null
        // isSubsidiaryDwelling: null,
        // numberOfSubsidiaryDwellings: null,
        // considerationSubsidiaryDwellings: null,
        // isOtherLand: null,
        // considerationOtherLand: null,
        // otherLandType: null,
        // isLinkedTransaction: null,
        // considerationLinkedTransaction: null
      };
      this.componentName = "TransactionRate";
    },
    getDate: function(stringDate) {
      return new Date(
        stringDate.split("/")[1] +
          "/" +
          stringDate.split("/")[0] +
          "/" +
          stringDate.split("/")[2]
      );
    },
    scrollUp: function() {
      if (window.pageYOffset > 378) {
        this.$scrollTo("#questionCard", 500, {});
      }
    }
  },
  computed: {},
  mounted() {
    this.getText();
    this.setStage();
  },
  components: {
    TransactionRate,
    TransactionDate,
    ConsiderationDwellings,
    NumberOfDwellings,
    IsSubsidiaryDwelling,
    NumberOfSubsidiaryDwellings,
    ConsiderationSubsidiaryDwellings,
    IsOtherLand,
    ConsiderationOtherLand,
    OtherLandType,
    IsLinkedTransaction,
    ConsiderationLinkedTransaction,
    TaxCalculation
  }
};
</script>

<style scoped>
.fade-enter-active {
  transition: opacity 0.25s ease;
}

.fade-leave-active {
  transition: opacity 0s ease;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.fade-enter-to,
.fade-leave {
  opacity: 1;
}
</style>
