<template>
  <wra-card
    :class="{
      'px-1 py-1': $vuetify.breakpoint.smAndDown,
      'px-6 py-5': $vuetify.breakpoint.mdAndUp
    }"
    color="#F1F1F1"
    outlined
    v-if="this.totalTax != null"
  >
    <transition name="translate" mode="out-in">
      <div :key="text.taxCalculation.title">
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{ text.taxCalculation.title }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pa-0">
            <p v-html="text.taxCalculation.description"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- v-if Route 1 or 2 -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'Residential' ||
              localInputs.propertyType == 'Freehold' ||
              localInputs.propertyType == 'ExistingLease'
          "
        >
          <wra-card-text class="calc-result">
            <v-row class="ma-0">
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalTaxDue }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatCurrency(totalTax) }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.effectiveDate }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  {{ localInputs.transactionDate }}
                </div>
              </v-col>
              <v-col cols="12" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.purchasePrice }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ localInputs.purchasePrice }}
                </div>
              </v-col>
            </v-row>
          </wra-card-text>
        </wra-card-text>

        <!-- v-if Route 3 -->
        <wra-card-text class="pa-4" v-else>
          <wra-card-text class="calc-result">
            <v-row class="ma-0">
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalTaxDue }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatCurrency(totalTax) }}
                </div>
              </v-col>
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.effectiveDate }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  {{ localInputs.transactionDate }}
                </div>
              </v-col>
            </v-row>
            <v-row class="ma-0">
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalPremium }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ localInputs.purchasePrice }}
                </div>
              </v-col>
              <v-col cols="12" xl="6" class="pa-0">
                <div class="pa-4 calc-result-header">
                  {{ text.taxCalculation.calcResult.totalNpv }}
                </div>
                <div
                  class="px-4 pt-0 pb-4"
                  :class="{
                    'calc-result-data-sm': $vuetify.breakpoint.smAndDown,
                    'calc-result-data': $vuetify.breakpoint.mdAndUp
                  }"
                >
                  &pound;{{ formatUseGrouping(npv) }}
                </div>
              </v-col>
            </v-row>
          </wra-card-text>
        </wra-card-text>

        <!-- 'Summary of your answers' expansion panel -->
        <wra-card-text class="pa-4">
          <wra-expansion-panels flat v-model="summaryPanel">
            <wra-expansion-panel>
              <wra-expansion-panel-header>
                {{ text.taxCalculation.answerSummary.title }}
              </wra-expansion-panel-header>

              <wra-expansion-panel-content class="pt-0">
                <wra-simple-table>
                  <template v-slot:default>
                    <caption>
                      {{
                        text.taxCalculation.answerSummary.title
                      }}
                    </caption>
                    <tbody>
                      <!--TransactionType-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionType
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionType == 'Residential'"
                        >
                          {{ text.transactionType.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionType == 'NonResidential'"
                        >
                          {{ text.transactionType.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionType')"
                            id="transactionTypeEdit"
                          >
                            {{ text.taxCalculation.answerSummary.edit }}
                          </wra-button>
                        </td>
                      </tr>
                      <!--TransactionRate-->
                      <tr v-if="localInputs.transactionType == 'Residential'">
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionRate
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'HigherRates'"
                        >
                          {{ text.transactionRate.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.transactionRate == 'MainRates'"
                        >
                          {{ text.transactionRate.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionRate')"
                            id="transactionRateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--PropertyType-->
                      <tr
                        v-if="localInputs.transactionType == 'NonResidential'"
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.propertyType }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'Freehold'"
                        >
                          {{ text.propertyType.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'NewLease'"
                        >
                          {{ text.propertyType.answers[1].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.propertyType == 'ExistingLease'"
                        >
                          {{ text.propertyType.answers[2].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('PropertyType')"
                            id="propertyTypeEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--TransactionDate-->
                      <tr>
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.transactionDate
                          }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.transactionDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TransactionDate')"
                            id="transactionDateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--LeaseTerm-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseStartDate }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.leaseStartDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseStartEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseEndDate }}
                        </td>
                        <td class="text-right">
                          {{ localInputs.leaseEndDate }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseEndEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.leaseDuration }}
                        </td>
                        <td class="text-right">
                          {{ leaseDisplay }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('LeaseTerm')"
                            id="leaseTermEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--CalculationDate-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.transactionDate !=
                              localInputs.leaseStartDate
                        "
                      >
                        <td class="pl-0">
                          {{
                            text.taxCalculation.answerSummary.calculationDate
                          }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.calculationDate == 'EffectiveDate'"
                        >
                          {{ text.calculationDate.answers[0].label }}
                        </td>
                        <td
                          class="text-right"
                          v-if="localInputs.calculationDate == 'LeaseStartDate'"
                        >
                          {{ text.calculationDate.answers[1].label }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('CalculationDate')"
                            id="calculationDateEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--RentInformation-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearOne }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearOne }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearOneEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearTwo != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearTwo }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearTwo }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearTwoEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearThree != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearThree }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearThree }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearThreeEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearFour != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearFour }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearFour }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearFourEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.rentYearFive != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.rentYearFive }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.rentYearFive }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="rentYearFiveEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease'
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.relevantRent }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.relevantRent }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="relevantRentEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <tr
                        v-if="
                          localInputs.transactionType == 'NonResidential' &&
                            localInputs.propertyType == 'NewLease' &&
                            localInputs.highestRent != null
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.highestRent }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.highestRent }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('RentInformation')"
                            id="highestRentEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--PurchasePrice-->
                      <tr
                        v-if="
                          localInputs.transactionType == 'Residential' ||
                            (localInputs.transactionType == 'NonResidential' &&
                              localInputs.propertyType != 'NewLease')
                        "
                      >
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.purchasePrice }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.purchasePrice }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('PurchasePrice')"
                            id="purchasePriceEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                      <!--TotalPremium-->
                      <tr v-else>
                        <td class="pl-0">
                          {{ text.taxCalculation.answerSummary.totalPremium }}
                        </td>
                        <td class="text-right">
                          &pound;{{ localInputs.purchasePrice }}
                        </td>
                        <td class="text-right pr-0">
                          <wra-button
                            small
                            dark
                            color="#0360a6"
                            class="hidden-print"
                            style="margin-right:2px;"
                            @click="editStage('TotalPremium')"
                            id="totalPremiumEdit"
                          >
                            {{
                              text.taxCalculation.answerSummary.edit
                            }}</wra-button
                          >
                        </td>
                      </tr>
                    </tbody>
                  </template>
                </wra-simple-table>
              </wra-expansion-panel-content>
            </wra-expansion-panel>
          </wra-expansion-panels>
        </wra-card-text>

        <!-- Detailed calculation -->
        <wra-card-text class="pa-4">
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{
              text.taxCalculation.calcBreakdown.title
            }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.description"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- LTT on the premium -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{
              text.taxCalculation.calcBreakdown.premiumTitle
            }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.premiumDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Purchase Price Bands Table (LTT on the premium) -->
        <wra-card-text class="pt-0 px-0 pb-4">
          <wra-data-table
            :headers="basedOnPremiumHeaders"
            :items="bands"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:item.amount="{ item }">
              {{ formatUseGrouping(item.amount) }}
            </template>

            <template v-slot:item.tax="{ item }">
              {{ formatCurrency(item.tax) }}
            </template>

            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td
                    colspan="4"
                    class="text-right"
                    v-if="
                      localInputs.transactionType == 'Residential' ||
                        (localInputs.transactionType == 'NonResidential' &&
                          localInputs.propertyType != 'NewLease')
                    "
                  >
                    {{ text.taxCalculation.calcBreakdown.totalTaxDue }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnPremium) }}
                  </td>
                  <td colspan="4" class="text-right" v-else>
                    {{ text.taxCalculation.calcBreakdown.basedOnPremium }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnPremium) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <!-- LTT on the net present value (NPV) of rent -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{
              text.taxCalculation.calcBreakdown.npvTitle
            }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.npvDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Rent Bands/NPV Table (LTT on the NPV of rent) -->
        <wra-card-text
          class="pt-0 px-0 pb-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-data-table
            :headers="basedOnNpvHeaders"
            :items="npvBands"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:item.amount="{ item }">
              {{ formatUseGrouping(item.amount) }}
            </template>

            <template v-slot:item.tax="{ item }">
              {{ formatCurrency(item.tax) }}
            </template>

            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td colspan="4" class="text-right">
                    {{ text.taxCalculation.calcBreakdown.basedOnNpv }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(taxBasedOnNpv) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <!-- Tax chargeable -->
        <wra-card-text
          class="pa-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-card-title class="pt-0 px-0 pb-4">
            <wra-header size="h2">{{
              text.taxCalculation.calcBreakdown.subTotalCalcTitle
            }}</wra-header>
          </wra-card-title>
          <wra-card-text class="pt-0 px-0 pb-4">
            <p v-html="text.taxCalculation.calcBreakdown.subTotalCalcDescr"></p>
          </wra-card-text>
        </wra-card-text>

        <!-- Tax Chargeable Table -->
        <wra-card-text
          class="pt-0 px-0 pb-4"
          v-if="
            localInputs.transactionType == 'NonResidential' &&
              localInputs.propertyType == 'NewLease'
          "
        >
          <wra-data-table
            :headers="taxChargeableHeaders"
            :items="taxChargeableItems"
            disable-filtering
            disable-pagination
            disable-sort
            hide-default-footer
            :class="{
              'px-0': $vuetify.breakpoint.smAndDown
            }"
          >
            <template v-slot:foot>
              <tfoot>
                <tr class="calc-breakdown-total">
                  <td colspan="3" class="text-right">
                    {{ text.taxCalculation.calcBreakdown.totalTaxDue }}
                    {{ "\xa0" }}&pound;{{ formatCurrency(totalTax) }}
                  </td>
                </tr>
              </tfoot>
            </template>
          </wra-data-table>
        </wra-card-text>

        <wra-card-text class="pa-4">
          <p>
            {{ text.taxCalculation.calcBreakdown.payLtt.textP1 }}
            <a
              :href="text.taxCalculation.calcBreakdown.payLtt.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.calcBreakdown.payLtt.linkText +
                  ' (' +
                  text.taxCalculation.calcBreakdown.payLtt.newTab +
                  ')'
              "
              >{{ text.taxCalculation.calcBreakdown.payLtt.linkText }}</a
            >
            {{ text.taxCalculation.calcBreakdown.payLtt.textP2 }}
          </p>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <wra-button outlined @click="printClicked()" id="printButton">
            {{ text.taxCalculation.print }}
          </wra-button>
        </wra-card-text>

        <wra-card-text class="hidden-print">
          <p>
            <a
              :href="text.taxCalculation.feedback.link"
              target="_blank"
              rel="noreferrer"
              :aria-label="
                text.taxCalculation.feedback.text +
                  ' (' +
                  text.taxCalculation.feedback.newTab +
                  ')'
              "
              >{{ text.taxCalculation.feedback.text }}</a
            >
          </p>
        </wra-card-text>

        <wra-card-actions class="pt-4 px-4 pb-2 hidden-print">
          <v-row>
            <v-col cols="12" md="2" order="first" order-md="last">
              <wra-button
                dark
                class="mb-2"
                @click="firstStage()"
                id="restartButton"
              >
                {{ text.restart }}
              </wra-button>
            </v-col>
            <v-col cols="12" md="2" order="last" order-md="first">
              <wra-button
                dark
                class="mr-4 mb-2"
                @click="previousStage"
                id="backButton"
              >
                <wra-icon alt="" left large>navigate_before</wra-icon>
                <span v-html="text.previous"></span>
              </wra-button>
            </v-col>
          </v-row>
        </wra-card-actions>
      </div>
    </transition>
  </wra-card>
</template>

<script>
const axios = require("axios");

import lttCalculation from "../../mixins/ltt-calculation.js";
import npvCalculation from "../../mixins/npv-calculation.js";
import getDate from "../../mixins/get-date.js";
import describeBands from "../../mixins/describe-bands.js";
import formatUseGrouping from "../../mixins/format-use-grouping.js";
import formatCurrency from "../../mixins/format-currency.js";
import calcBreakdownHeaders from "../../mixins/calc-breakdown-headers.js";

export default {
  props: ["text", "inputs", "leaseDuration", "leaseDisplay"],

  mixins: [
    lttCalculation,
    npvCalculation,
    getDate,
    describeBands,
    formatUseGrouping,
    formatCurrency,
    calcBreakdownHeaders
  ],

  data: () => ({
    localInputs: {},
    rates: null,
    relevantRent: null,
    bands: [],
    npvBands: [],
    totalTax: null,
    taxBasedOnPremium: null,
    taxBasedOnNpv: null,
    npv: null,
    summaryPanel: []
  }),
  watch: {
    text: {
      deep: true,
      handler() {
        this.describeBands();
        if (
          this.localInputs.transactionType == "NonResidential" &&
          this.localInputs.propertyType == "NewLease"
        ) {
          this.describeNpvBands();
        }
      }
    },
    summaryPanel: function(newValue) {
      var label;

      if (newValue == null) {
        label = "Close";
      } else if (newValue == 0) {
        label = "Open";
      }
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Summary", label);
      }
    }
  },
  computed: {
    basedOnNpvHeaders: function() {
      return [
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.rentBands +
            " (£)",
          value: "description"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.percentageRate +
            " (%)",
          value: "rate",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.amount + " (£)",
          value: "amount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "tax",
          align: "right"
        }
      ];
    },
    taxChargeableHeaders: function() {
      return [
        {
          text: this.text.taxCalculation.calcBreakdown.tableHeaders.type,
          value: "type"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.totalAmount +
            " (£)",
          value: "totalAmount",
          align: "right"
        },
        {
          text:
            this.text.taxCalculation.calcBreakdown.tableHeaders.taxDue + " (£)",
          value: "taxDue",
          align: "right"
        }
      ];
    },
    taxChargeableItems: function() {
      return [
        {
          type: this.text.taxCalculation.calcBreakdown.rowDescriptions
            .basedOnPremium,
          totalAmount: this.localInputs.purchasePrice,
          taxDue: this.formatCurrency(this.taxBasedOnPremium)
        },
        {
          type: this.text.taxCalculation.calcBreakdown.rowDescriptions
            .basedOnNpv,
          totalAmount: this.formatUseGrouping(this.npv),
          taxDue: this.formatCurrency(this.taxBasedOnNpv)
        }
      ];
    }
  },
  methods: {
    previousStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Tax calculation", "Back");
      }
      this.$emit("previous", this.localInputs);
    },
    firstStage: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Start again");
      }
      this.$emit("reset");
    },
    describeNpvBands: function() {
      //taxBasedOnNpv
      this.npvBands.forEach(function(band, index, npvBands) {
        if (index == 0) {
          //First row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.upTo +
            " " +
            this.formatUseGrouping(band.max);
        } else if (index == npvBands.length - 1) {
          //Last row
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1);
        } else {
          band.description =
            this.text.taxCalculation.calcBreakdown.rowDescriptions.above +
            " " +
            this.formatUseGrouping(band.min - 1) +
            " " +
            this.text.taxCalculation.calcBreakdown.rowDescriptions.andUpTo +
            " " +
            this.formatUseGrouping(band.max);
        }
      }, this);
    },
    calculateTotal: function() {
      this.totalTax = this.taxBasedOnPremium + this.taxBasedOnNpv;
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event(
          "LTT Calculator",
          "Tax calculated",
          null,
          Math.round(this.totalTax)
        );
      }
    },
    editStage: function(stage) {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Edit", stage);
      }
      this.$emit("goToStage", {
        localInputs: this.localInputs,
        componentName: stage
      });
    },
    printClicked: function() {
      if (this.$root.$data.cookieAccept.websiteUse == "true") {
        this.$ga.event("LTT Calculator", "Print or Save");
      }
      window.print();
    }
  },
  created() {
    this.localInputs = JSON.parse(JSON.stringify(this.inputs));
  },
  mounted() {
    this.totalTax = null; // reset the tax amount to null before calculating (or recalculating on second visit)
    axios.get("./rates/rates.json").then((response) => {
      this.rates = response.data.rates;
      this.relevantRent = response.data.relevantRent;
      this.calculate(this.localInputs);
      if (
        this.localInputs.transactionType == "NonResidential" &&
        this.localInputs.propertyType == "NewLease"
      ) {
        this.calculateNpv(this.localInputs);
      }
      this.calculateTotal();
      this.$nextTick(() => {
        this.$emit("scrollUp");
      });
    });
  }
};
</script>
